"use client";

import { Header } from "../navigation/header";
import { Sidebar } from "../navigation/sidebar";
import { useIntlayer } from "next-intlayer";
import { useCallback, useEffect, useState } from "react";
import { AxiosErrorProps, PlansProps } from "@/types";
import { getError, getRequest } from "@/utils/axios-instance";
import { ENDPOINTS } from "@/shared/site";
import { useAlertStore } from "@/providers/alert";
import { SubManage } from "./modal/manage";
import { Switch } from "@heroui/react";
import { SubList } from "./list";
import { SubInfoTabs } from "./info/tabs";
import { SubInfoTable } from "./info/table";

export function Sub() {
  const content = useIntlayer("sub-content");
  const [isYearly, setIsYearly] = useState(false);
  const [isLoading, setIsLoading] = useState(true);
  const [plans, setPlans] = useState<PlansProps[]>([]);
  const { setAlert } = useAlertStore();

  const getPlans = useCallback(async () => {
    try {
      const { data } = await getRequest({ url: ENDPOINTS.plans });
      if (data) setPlans(data);
    } catch (e) {
      const { data, status } = getError(e as AxiosErrorProps);
      if (status && status >= 500)
        setAlert((prev) => ({
          ...prev,
          color: "danger",
          description: content.errors.server.value,
          isVisible: true,
        }));
      else
        setAlert((prev) => ({
          ...prev,
          color: "danger",
          description: data.message,
          isVisible: true,
        }));
    } finally {
      setIsLoading(false);
    }
  }, [plans]);

  useEffect(() => {
    getPlans();
  }, []);

  return (
    <main className="flex h-svh">
      <Sidebar />
      <div className="flex-1">
        <Header title={content.title.value} />
        <div className="h-[calc(100svh-65px)] bg-white dark:bg-black overflow-y-auto p-8">
          {/* <div className="flex justify-between space-x-8">
            <SubManage />
            <div className="flex items-center space-x-2">
              <span
                className={`text-sm ${
                  isYearly ? "text-primary" : "text-default-500"
                }`}
              >
                {content.range.year}
              </span>
              <Switch
                size="sm"
                isSelected={isYearly}
                onValueChange={setIsYearly}
              />
            </div>
          </div> */}
          {/* <SubList isYearly={isYearly} plans={plans} isLoading={isLoading} /> */}
          <div className="space-y-8">
            {/* <SubInfoTabs /> */}
            {/* <SubInfoTable plans={plans} isLoading={isLoading} /> */}
          </div>
        </div>
      </div>
    </main>
  );
}
